import React from 'react';
import { Link } from 'react-router-dom';

import styles from './styles.sass';

const ProfileDropdown = ({ isActive, exitOfApplication, profileRef }) =>
  <div ref={profileRef} hidden={isActive} className={styles.ProfileDropdownShow}>
    <Link to="/profilesettings"></Link>
    <Link to="/helps"></Link>
    <Link className={styles.exitLink} onClick={exitOfApplication} to="/login"></Link>
  </div>

export default ProfileDropdown;
