import isEmpty from "lodash/isEmpty";

const MIN_LENGTH_CHARACTER = 4;

const CONVERT_NAME_FIELD = {
  login: "",
  password: ""
};

export const validateInput = (nameField, symbols) => {
  const errors = {};

  if (symbols.length < MIN_LENGTH_CHARACTER) {
    errors[nameField] = `${
      CONVERT_NAME_FIELD[nameField]
    }     4 `;
  }

  return {
    errors
  };
};

export const validateAllInputs = ({ login = "", password = "" }) => {
  const errors = {};

  if (login.length < MIN_LENGTH_CHARACTER) {
    errors.login = "     4 ";
  }

  if (password.length < MIN_LENGTH_CHARACTER) {
    errors.password = "     4 ";
  }

  return {
    errors,
    isValid: isEmpty(errors)
  };
};
