import React from 'react';

import styles from './style.sass';

const expenseScopes = [
  {
    enName: 'food',
    ruName: ':',
  },
  {
    enName: 'household',
    ruName: ':',
  },
  {
    enName: 'travel',
    ruName: ':',
  },
  {
    enName: 'clothes',
    ruName: ':',
  },
  {
    enName: 'rent',
    ruName: ':',
  },
  {
    enName: 'mobile',
    ruName: ':',
  },
];

const CostsList = ({ expenseScope }) => (
  <ul className={styles.costLists}>
    {expenseScopes.map((item, index) => (
      <li key={index}>
        {item.ruName}
        <span>{expenseScope[item.enName]}</span> .
      </li>
    ))}
  </ul>
);

export default CostsList;
